.c-section {
  position: relative;
  width: 100vw;
}
#sticky-wrapper {
  display: inline-block;
  vertical-align: top;
  //white-space: nowrap;
  height: 100%;
}
.c-section.-long {
  width: auto;
  //padding: 0 0 0 70px;
  margin-right: 70px;
}

