.c-section_wrapper {
  position: relative;
  display: flex;
  height: 100%;
  align-items: center;
  white-space: normal;
  padding: 3.75rem;
}

.c-section_content {
  position: relative;
  width: 25rem;
}

.c-section_list {
  width: calc(100vw - 70px);
  margin-left: 70px;
  //background-color: #222;
  flex-direction: column;
  padding: 0;
}

.c-list {
  position: relative;
  list-style: none;
  overflow: hidden;
}

.c-list_wrapper {
  width: 100%;
}
.sticky_bg {
  position: absolute;
  z-index: 0;
  width: calc(100vw - 70px);
  margin-left: 70px;
  height: 100%;
  .c-header {
    width: 100vw;

    color: #ffffff;
    font-size: 36px;
    line-height: 40px;
    letter-spacing: -1px;
    text-transform: uppercase;
    padding: 70px 3.75rem 0;

    h2 {
      font-family: 'Aeroport';
      font-weight: 400;
    }
  }
}
.c-list_image_bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
  img {
    width: 100%;
  }
}
.c-list_item {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  flex-direction: column;
  color: #f5f5f5;
  background-color: #222;

  min-height: 100vh;
  border-top: 1px solid;
  overflow: hidden;
}

.c-list_head {
  display: flex;
  height: 60px;
  padding: 0 3.75rem;
  align-items: center;

  font-size: 24px;
  line-height: 30px;
  background-color: #222222;
  font-family: 'Aeroport';
  letter-spacing: -1px;
  text-transform: uppercase;
  font-weight: 400;
}
.c-list_content {
  border-top: 1px solid;
  padding: 40px 3.75rem;
  font-size: 16px;
  line-height: 28px;
  min-height: calc(100vh - 60px);
  font-family: "Open Sans";
}
.c-list_image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100%;
  object-fit: cover;
  z-index: -1;

}

.c-heading {
  margin-bottom: 1.25rem;
  text-transform: uppercase;
  font-weight: bold;
}
.img_block {
  width: calc(80% - 30px);
  img {
    width: auto;
    height: auto;
    padding-right: 30px;
    object-fit: cover;
    max-width: 100%;
    max-height: 100%;
  }
}
.text_block {
  width: 100%;
  padding: 0 70px 0 30px;
  font-size: 16px;
  line-height: 28px;
}
@media (max-width: 1153px) {
  #brand {
    display: none;
  }
}
