.educationPage{
  padding: 30px 55px 125px 100px;
  width: 100vw;
  box-sizing: border-box;
  height: auto;
}
.educationPage__logo__svg{
  display: block;
  width: 122px;
}
.title--educationPage{
  padding-top: 100px;
}
.education__content{
  padding-top: 60px;
}
.education__list__content{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
}
.education__list{
  display: flex;
  padding-top: 33px;
  border-top: 1px solid #DEDEDE;
  margin-bottom: 36px;
  position: relative;
  &__description{
    font-family: "Open Sans";
    font-size: 16px;
    line-height: 24px;
    padding-top: 22px;
    color: #373737;
  }
}
.education__list__img{
  max-width: 34.2vw;
  & img{
    width: 100%;
  }
}
.education__list__description-container{
  padding-left: 2.87vw;
  max-width: 41vw;
}
.education__list__title{
  font-size: 24px;
  line-height: 30px;
  text-transform: uppercase;
  color: #373737;
}
.education__arrow__svg{
  position: absolute;
  right: 0;
  top: 35px;
  width: 25px;
}
.education__list__href{
  padding-left: 2.87vw;
  display: flex;
  justify-content: space-between;
}
.education__list__href__decoration{
  color: #373737;
  display: inline-block;
  border-bottom: 1px solid #373737;
  font-size: 16px;
  line-height: 30px;
}
.education__list-date{
  font-size: 16px;
  line-height: 23px;
  letter-spacing: -1px;
  text-transform: uppercase;

  color: #373737;
}
.educationPage__href{
  margin-top: 130px;
  display: flex;
  justify-content: center;
  align-items: center;
  &__decoration{
    font-weight: 500;
    font-size: 15px;
    line-height: 30px;
    color: #1E1E1E;
    display: inline-block;
    padding: 11px 21px;
    border-radius: 50%;
    transition: 0.3s  background-color linear;
    will-change: background-color;
    &:first-child{
      padding: 10px 22px;
    }
    &--active{
      background-color: #C4C4C4;
    }
  }
}
.new-one__article .hero-desc__date{
  padding: 30px 0 20px;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -1px;
  text-transform: uppercase;
}
.new-one__article .news__desc__text-decoration{
  font-size: 24px;
  line-height: 30px;
}
.new-one__article .news__desc__container{
  color: #373737;
}