.brand-mobile{
  display: none !important;
}
@media (max-width: 1153px) {
  .brand-mobile{
    display: block !important;
    &-content{
      margin-top: 40px;
      width: 100vw;
      min-height: 1300px;
      background: url("./img/mobile_brand.jpg") no-repeat bottom;
      background-size: cover;
      color: #D9D9D9;
      display: flex;
      align-items: center;
      position: relative;
      &:after{
        content: '';
        display: block;
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0%;
        background-color: rgba(0,0,0,0.4);
        z-index: 1;
      }
    }
    & .wrapper{
      width: 100%;
    }
  }
  .title--mobile-brand{
    display: block;
    padding: 120px 0 60px 0;
    margin-left: 30px;
    margin-right: 60px;
    border-bottom: 1px solid #DEDEDE;
    &__decoration{
      display: inline-block;
      padding-bottom: 2px;
      border-bottom: 2px solid #373737;
    }
  }
  .brand-mobile-list__content{
    max-height: 0;
    min-height: 0;
    overflow: hidden;
    transition: 0.5s all linear;
    will-change: max-height;

  }
  .mobile-content__description{
    font-family: Open Sans;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 28px;
    padding: 60px 30px 0 35px;
    transition: 0.8s padding linear 0.3s;
  }
  .mobile-content__description:last-child{
    padding-top: 20px;
  }
  .show {
    & .brand-mobile-list__content{
      display: block;
      height: auto;
      max-height: 600px;
      min-height: 100%;
      transition: 0.5s all linear ;
      will-change: max-height;
      & .mobile-content__description{
        padding-top: 60px;
        transition: 0.8s padding linear 0.3s;
      }
      & .mobile-content__description:last-child{
        padding-top: 20px;
      }
    }
  }

  .brand-mobile-lists{
    box-sizing: border-box;
    width: 100%;
  }
  .brand-mobile-list{
    padding: 36px 0;
    border-bottom: 1px solid white;
    position: relative;
    background: transparent;
    z-index: 12;
    overflow: hidden;
  }
  .brand-mobile-list__text{
    padding: 0 35px 0 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 24px;
    line-height: 30px;
    letter-spacing: -1px;
    text-transform: uppercase
  }
  .brand-item-plus{
    height: 30px;
    width: 18px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    & .brand-item-plus__line{
      display: block;
      width: 100%;
      height: 2px;
      background-color: #FFFFFF;
      transition: 0.5s transform linear;
    }
    & .brand-item-plus__line:last-child{
      transform: translateY(-2px) rotate(90deg);
    }
  }
  .mobile-content-logo{
    padding:90px 30px 0 35px;
  }
  .mobile-content-logo__svg{
    width: 261px;
  }
  .show  .brand-item-plus__line:last-child{
    transform: translateY(-2px) rotate(0deg);
  }
}


