.education-mobile{
  display: none !important;
}

@media (max-width: 1025px) {
  .education-mobile{
    display: block !important;
  }
  .education-mobile .title--education-mobile{
    padding-top: 140px;
    margin-left: 30px;
  }
  .education-mobile .direction__title-sub{
    border-top: 1px solid #ededed;
    padding-top: 60px;
    margin: 60px 30px 0 30px;
    color: #373737;
    padding-left: 0px;
    position: relative;
    font-size: 24px;
    line-height: 30px;
  }
  .education-mobile .direction__content{
    margin-top: 80px;
    background: url("img/mobile_bg.jpg"),  no-repeat;
    background-size: cover;
    position: relative;
  }
  .education-mobile{
    & .standard-description-education{
      display: block;
      width: 100%;
      padding: 80px 30px;
    }
    & .standard-description__container__text:last-child{
      margin: 0;
      max-width: 100%;
      padding: 40px 0 60px 0;
    }
    .education-mobile-img{
      & img{
        display: flex;
        width: 100%;
        height: 100%;
      }
    }
  }
  .category-title--news__decoration{
    display: inline-block;
    padding-bottom: 2px;
    border-bottom: 2px solid #373737;
  }
}


