.section-contact-wrapper{
  margin-top: 220px;
  padding-bottom: 110px;
}
.contact-section-container{
  margin-top: 60px;
  border-top: 1.13151px solid #DEDEDE;
  padding-top: 26px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 174px;
}
.category-link--section{
  position: initial;
  font-size: 24px;
  line-height: 30px;
  letter-spacing: -1px;
  text-transform: uppercase;
}
.contact-items-section .contact__list--section:last-child{
  margin: 0;
}

// navigation
.educationPage .navigation{
  border: 1px solid #DEDEDE;
}
.navigation--education{
  position: fixed;
  top: 0;
  right: 0;
}
.navigation--education .nav-small__content__decoration__line{
  background-color: #373737;
}
.navigation--education .nav-small__text{
  color: #373737;
}
.navigation--education .nav-small__lang{
  color: #373737;
}