.news-one-page{
  padding: 30px 0 125px 100px;
  box-sizing: border-box;
  width: 100vw;
  color: #373737;
}
.news-one-page .navigation{
  border: 1px solid #dedede;
}
.news-one-page-logo{
  display: block;
  width: 122px;
}
.news-one-page-title{
  margin-top: 100px;
  padding-right: 55px;
  padding-bottom: 60px;
}
.news-one-page{
  display: inline-block;
}
.news-one-page__svg{
  display: inline-block;
  margin-right: 46px;
  width: 30px;
  transform: rotate(270deg);
}
.news-one-page-container{
  margin-top: 60px;
  border-top: 1px solid #dedede;
  border-bottom: 1px solid #dedede;
  padding-top: 44px;
  padding-bottom: 50px;
  margin-right: 55px;
}
.news-one-page-wrapper-custom {
  & h2, h3 {
    font-size: 24px;
    line-height: 30px;
    text-transform: uppercase;
    color: #373737;
    font-weight: normal;
    letter-spacing: -1px;
    font-style: normal;
    font-weight: normal;
    padding-top: 40px;
    border-top: 1px solid #dedede;
  }
  & h3{
    border: none;
    padding:15px 0;
    font-size: 18px;
    line-height: 24px;
  }

  & p {
    font-family: "Open Sans";
    font-size: 16px;
    line-height: 29px;
    padding-bottom: 20px;
  }

  & b {
    font-size: 16px;
    line-height: 23px;
    text-transform: uppercase;
    letter-spacing: -1px;
    margin: 30px 0;
    display: inline-block;
    font-weight: normal;
  }

  & ul {
    padding: 40px 0 60px 0;
    font-family: "Open Sans";
    font-size: 16px;
    line-height: 29px;

    & li {
      position: relative;
      display: flex;
      align-items: flex-start;

      &:before {
        content: "";
        position: absolute;
        top: 15px;
        left: 0;
        margin-right: 25px;
        display: block;
        width: 8px;
        height: 1px;
        background-color: #373737;
      }
      & p{
        font-family: Open Sans;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 29px;
        color: #373737;
        padding-left: 25px;
      }
      & strong{
        display: block;
        font-family: Open Sans;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 29px;
      }
    }
  }
  & section{
    padding-top: 40px;
    border-top: 1px solid #DEDEDE;
    border-bottom: 1px solid #DEDEDE;
    padding-bottom: 53px;
    margin-right: 50px;
    img {
      display: flex;
      width: 100%;
    }
  }
  & .new-one-slider-container{
    padding-top: 60px;
  }
  & .news__desc__container .news__desc__text .news__desc__text-decoration{
    font-size: 24px;
    line-height: 30px;
    padding-bottom: 0;
  }
  & .news-one-page-container-slider{
    padding-top: 180px;
  }
}
.news-one-page-container__news-one{
  padding-top: 260px;
}



