$black: #373737;
.about{
  height: 100vh;
  width: 100vw;
}
.standard-description{
  padding: 70px 80px 75px 110px;
  overflow: hidden;
  position: relative;
  box-sizing: border-box;

}
.category-sub-title{
  padding-top: 60px;
  font-size: 36px;
  letter-spacing: -1px;
  text-transform: uppercase;
  font-weight: normal;
  font-style: italic;
  &__decoration{
    display: block;
  }
}

.standard-description__container{
  padding-top: 60px;

  &__text{
    font-family: "Open Sans";
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 32px;
    max-width: 800px;
  }
}
.category-sub-title--mobile{
  display: none;
}
.standard-description__container .standard-description__container__text:last-child{
  margin-top: 40px;
  margin-left: auto;
  padding-right: 150px;
}
.category-link{
  position: absolute;
  bottom: 74px;
  right: 70px;
  display: flex;
  width: fit-content;
  align-items: center;
  font-size: 36px;
  line-height: 36px;
  letter-spacing: -1px;
  text-transform: uppercase;
  color: $black;
  font-style: italic;
  & .list-items__href__decoration{
    transform: translateY(0);
    margin-left: 30px;
  }
}
.category-link__text{
  padding-bottom: 2px;
  border-bottom: 2px solid $black;
}
.standard-description__container__text--last{
  max-width: 750px;
}
@media (max-width: 1680px) {
  .standard-description{
    width: 105.5vw;
    padding: 60px 80px 65px 80px;
  }
  .title{
    font-size: 30px;
    line-height: 36px;
  }
  .category-sub-title, .standard-description__container{
    padding-top: 40px;
  }
  .category-sub-title{
    font-size: 30px;
    line-height: 36px;
    max-width: 1288px;
  }

  .standard-description__container__text{
    font-size: 16px;
    line-height: 29px;
    max-width: 653px;
  }
  .standard-description__container .standard-description__container__text:last-child{
    padding-left: 20px;
    margin-top: 20px;
    max-width: 638px;
    padding-right: 0;
    margin-right: 129px;
    box-sizing: border-box;
  }
  .category-link{
    bottom:60px
  }
}
@media (max-width: 1439px) and (min-width: 1152px) {
  .standard-description__container .standard-description__container__text:last-child{
    margin-right: 8.95vw;
  }
  .title, .category-sub-title-desctop{
    font-size: clamp(22px, 28px, 32px);
    line-height: 120%;
  }
  .standard-description__container__text{
    font-size: clamp(11px, 14px, 16px);
    line-height: 175%;
  }
  .category-link{
    font-size: clamp(22px, 28px, 32px);
    line-height: 100%;
    &__svg{
      width: 40%;
      height: 40%;
    }
  }
  .about .category-link .list-items__href__decoration {
    width: 80px;
  }
}
@media (max-width: 1153px){
  .about{
    padding: 30px 30px 42px 30px;
    width: 100%;
    height: auto;
    & .title{
      padding-top: 50px;
      font-size: 36px;
      line-height: 36px;
    }
    & .category-sub-title{
      font-size: 30px;
      line-height: 36px;
    }
    & .category-sub-title-desctop{
      display: none;
    }
    & .category-sub-title--mobile{
      display: block;
    }
    & .standard-description__container__text{
      max-width: 100%;
    }
    & .standard-description__container__text{
      margin-right: 0;
    }
    & .standard-description__container__text:last-child{
      padding: 46px 0 0 0;
      margin: 0;
      max-width: 100%;
    }
  }
  .category-link{
    display: none;
  }
  .category-link .list-items__href__decoration{
    display: none;
  }
}