$lightGray:#C4C4C4;
$dark:#2B2B2B;
$black:#373737;
.competence{
  position: relative;
  overflow: hidden;
}
.competence__content{
  display: flex;
  flex-direction: column;
  position: relative;
  justify-content: space-between;
  align-content: space-between;
  height: 100vh;
  padding: 70px 110px 70px 110px;
  box-sizing: border-box;
}
.competence-wrapper{
  display: grid;
  grid-template-columns: 105.27vw 43.75vw;
  white-space: initial;
  position: relative;
}
.competence_sub-title{
  padding-top: 80px;
  color: $lightGray;
  position: relative;
  font-size: 1.3vw;
  line-height: 125%;
  letter-spacing: -1px;
  text-transform: uppercase;
  max-width: 1330px;
  &:before{
    content: "";
    display: block;
    width: 100%;
    background-color: $lightGray;
    position: absolute;
    left: 0;
    top: 25%;
    height: 1px;
  }
}
.competence__list{
  margin-top: 25px;
  padding-bottom: 25px;
  border-bottom: 1px solid #DEDEDE;
  cursor: pointer;
  &:first-child{
    margin-top: 0;
  }
  &:hover .competence__list__svg{
    stroke: $black;
  }
}
.competence__list__text{
  width: 100%;
  justify-content: space-between;
  display: flex;
  align-items: center;
  text-transform: uppercase;
  font-size: 24px;
  line-height: 30px;
  letter-spacing: -1px;
  text-transform: uppercase;
  color: $dark;
  &__decoration{
    display: inline-block;
    width: 32px;
    height: 33px;
    overflow: hidden;
  }
}
.competence__list__svg{
  width: 100%;
  height: 100%;
  stroke: #DEDEDE;
  transition: 0.5s stroke linear;
  transform: scale(1.35);
}
.competence__img{
  height: 100vh;
  & img{
    display: flex;
    width: 100%;
    height: 100%;
  }
}
@media (max-width: 1680px) {
  .competence__content{
    padding: 60px 80px 65px 80px;
  }
  .competence_sub-title{
    padding-top: 60px;
  }
  .competence__list__text{
    font-size: 24px;
    line-height: 30px;
  }
}
.for-scroll{
  position: absolute;
  right: 34.4%;
  height: 5px;
  width: 5px;
}
@media (max-width: 1439px) and (min-width: 1152px) {
  .competence_sub-title{
    font-size: clamp(22px, 28px, 32px);
    line-height: 120%;
  }
  .competence__list__text{

  }
}
@media (max-width: 1153px) {
  .competence__content{
    justify-content: initial;
    padding: 120px 30px 30px 30px;
    height: auto;
  }
  .competence-wrapper{
    display: block;
  }
  .competence_sub-title{
    padding-top: 100px;
    font-size: 24px;
    line-height: 30px;
    padding-bottom: 90px;
  }
  .competence_sub-title:before{
    top: 30%;
  }
  .competence__list{
    padding-bottom: 30px;
  }
  .competence__list__text{
    font-size: 24px;
    line-height: 30px;
    letter-spacing: -1px;
  }
  .competence__img{
    display: none;
  }
}
