.standard-description-education{
  width: 100vw;
  position: relative;
}
.for-scroll--education{
  right: 45.4%;
}
.standard-description__container__text--education{
  max-width: 750px;
}
.standard-description__container__text--education-last{
  max-width: 789px;
}
@media (max-width: 1680px) {
  .standard-description-education{
    width: 112.2vw;
    & .standard-description__container{
      padding-top: 50px;
    }
  }
  .standard-description__container__text--education{
    max-width: 651px;
    font-size: 16px;
    line-height: 28px;
  }
}
@media (max-width: 1025px) {
  .standard-description-education{
    display: none;
  }
}