.contact{
  padding: 70px 110px;
  box-sizing: border-box;
  width: 95vw;
  position: relative;
  height: 100vh;
}
.contact-content{
  display: flex;
  border-top: 1px solid #DEDEDE;
  justify-content: space-between;
  margin-top: 40px;
}
.contact .contact-items{
  margin-top: 50px;
}
.contact__list:last-child{
  margin-top: 30px;
}
.contact__list__href{
  font-size: 24px;
  line-height: 32px;
  letter-spacing: -1px;
  text-transform: uppercase;
  color: #373737;
}
.contact__img_container{
  margin-top: 50px;
  max-width: 715px;
  & img{
    width: 100%;
  }
}
.category-link--contact{
  max-width: 774px;
  font-size: 36px;
  line-height: 36px;
  letter-spacing: -1px;
  font-weight: 400;
  font-style: italic;
}
.category-link__text--last{
  display: inline-block;
  margin-top: 6px;
}
@media (max-width: 1680px) {
  .contact{
    width: 104.23vw;
    padding: 60px 90px 60px 60px;
  }
  .contact__img_container{
    max-width: 656px;
  }
  .category-link--contact{
    max-width: 656px;
    font-size: 30px;
    line-height: 36px;
    letter-spacing: -1px;
    font-weight: normal;
    align-items: flex-start;
  }
  .contact .category-link .list-items__href__decoration{
    margin-left: 0px;
  }
  .contact .category-link{
    right: 80px;
  }
}
.for-scroll-contact{
  right: 0%;
}
.for-scroll-contact-first{
  right: auto;
  left: 0;
}

@media (max-width: 1025px) {
  .contact{
    width: 100%;
    height: auto;
    padding: 235px 30px 150px 30px;
    & .category-link{
      padding: 0;
      border: none;
      font-style: normal;
      font-weight: normal;
      font-size: 24px;
      line-height: 30px;
    }
    & .category-link__text{
      border: none;
    }
  }
  .contact__list:nth-child(2){
    display: none;
  }
  .contact__list:last-child{
    margin-top: 0;
  }
  .contact__list__href{
    display: inline-block;
    padding-bottom: 2px;
    border-bottom: 1px solid #373737;
  }
  .contact-content{
    flex-direction: column-reverse;
  }
  .contact__img_container {
    margin-top: 40px;
    & img{
      display: none;
    }
  }
}