.direction{
  width: 100vw;
  height: 100vh;
  background: url("img/decoration_bg.jpg"),  no-repeat;
  background-size: cover;
  position: relative;
  &__title{
    font-weight: normal;
    font-size: 36px;
    line-height: 36px;
    letter-spacing: -1px;
    text-transform: uppercase;
    box-sizing: border-box;
    color: #ffffff;
    padding-top: 70px;
    padding-left: 90px;
  }
  &__content{
    display: flex;
    height: calc(100vh - 106px);
    align-items: center;
  }
}
.direction__ways{
  width: 100%;
}
@media (max-width: 1680px) {
  .direction__title{
    font-size: 30px;
    line-height: 36px;
    padding-top: 60px;
    padding-left: 50px;
  }
  .competence__list--alternative{
    font-size: 24px;
    line-height: 30px;
    padding:20px 50px
  }
}
.for-scroll--direction{
  right: 4.5%;
}
@media (max-width: 1025px) {
  .direction{
    display: none;
  }
}