.hero{
  background: url("img/hero_bg.jpg"),  no-repeat;
  background-size: cover;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}
.navigation{
  width: fit-content;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 12;
}
.nav-small{
  width: 70px;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-right: 1px solid rgba(222, 222, 222, .85);
  position: relative;
  cursor: pointer;

  &__content{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 5px;
    &__decoration{
      margin-bottom: 41px;
      display: flex;
      justify-content: space-between;
      width: 6px;
      height: 30px;
      &__line{
        height: 100%;
        width: 2px;
        background-color: white;
      }
    }
  }
  &__text{
    color: white;
    font-size: 18px;
    line-height: 36px;
    letter-spacing: -1px;
    transform: rotate(-90deg);
    font-style: italic;
  }
}
.nav-small__lang{
  position: absolute;
  bottom: 36px;
  font-weight: 600;
  font-size: 14px;
  line-height: 19px;
  transform: rotate(270deg);
  color: white;
  z-index: 13;
  cursor: pointer;
}
.logo{
  padding:40px 0 0 130px;
}
.navigation-container-wrapper{
  width: 100vw;
  height: 100vh;
  position: absolute;
  top: 0;
  transform: translateX(-100%);
  transition: 0.75s transform linear;
  &--active{
    transform: translateX(0);
    transition: 0.75s transform linear;
  }
}
.navigation-container{
  background-color: white;
  width: 50vw;
  height: 100vh;
  position: relative;
  &__header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 60px 40px 0 70px;
  }
}
.logo-black{
  width: 122px;
  height: 31px;
}
.navigation-container__lang{
  display: flex;
  align-items: center;
  font-family: "Open Sans";
  font-size: 16px;
  line-height: 22px;

}
.navigation-container__lang__text{
  display: inline-block;
  padding: 0 5px;
  color: #373737;
  &--en{
    display: inline-flex;
    align-items: center;
    padding-right: 0;
    position: relative;
    color: #676767;
    &:before{
      content: "";
      display: block;
      height: 12px;
      width: 1px;
      background-color: #373737;
      position: absolute;
      left: 0;
    }
  }
}
.btn--close{
  display: inline-flex;
  align-items: center;
  background-color: transparent;
  padding: 0;
  margin-left: 20px;
  & svg{
    transform: translateY(2px);
  }
}
.navigation-list{
  padding: 16% 40px 0 70px;
  box-sizing: border-box;
}
.list-items{
  padding-bottom: 17px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  overflow: hidden;
  &:hover .list-items__href:after{
    width: 100%;
    transition: .5s width linear;
  }
  &:hover .list-items__href{
    transform: translateX(10px);
  }
  &:hover .list-items__href__text{
    color: #373737;
    transform: translate3d(10px, 0px, 0px) scale3d(1, 1, 1) rotateX(0deg) rotateY(0deg) rotateZ(0deg) skew(-8deg, 0deg);

  }
  &__href{
    display: inline-block;
    color: #373737;
    font-size: 24px;
    line-height: 30px;
    letter-spacing: -1px;
    text-transform: uppercase;
    transition: .5s transform linear;
    position: relative;
    transform-style: preserve-3d;
    &:after{
      content: "";
      display: block;
      position: absolute;
      left: 10px;
      width: 0;
      height: 1px;
      background-color: #373737;
      transition: 0.5s width linear;
    }
    &__text{
      display: inline-block;
      transition: .5s transform linear;
    }
    &__decoration{
      transform:  translateY(250%);
      transition: 1s  transform, opacity linear;
    }
    &:hover .list-items__href__decoration{
      transform: translateY(0);
    }
  }
}


.list-items:hover .list-items__href__decoration{
  transform:  translateY(0%);
}

.navigation-container__footer{
  max-width: 395px;
  font-size: 24px;
  line-height: 32px;
  text-transform: uppercase;
  position: absolute;
  right: 40px;
  bottom: 70px;
}
.link-action{
  display: block;
  color: #373737;
  letter-spacing: -1px;
  position: relative;
  &:after{
    content: "";
    display: block;
    width: 100%;
    position: absolute;
    //background-color: $black;
    height: 1px;
  }
}


//hero content
.hero_container{
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100vh;
  margin-left:70px;
  padding-left: 60px;
  white-space: initial;
}
.hero-logo{
  width: 141px;
  height: 36px;
}
.logo--white{
  padding-top: 40px;
}
.main-title-container{
  width: 50%;
}
.main-title-container--test{
  position: absolute;
  bottom: 180px;
  width: 50%;
}
.main-title{
  font-size: 130px;
  line-height: 110px;
  color: #D9D9D9;
  text-transform: uppercase;
  font-weight: 400;
  &__decoration{
    font-style: italic;
    display: block;
  }
  &__sub-title{
    color: #D9D9D9;
    font-size: 20px;
    line-height: 32px;
    font-family: "Open Sans";
    padding-top: 20px;
    &__decoration{
      display: block;
    }
  }
}

// new_slider
.hero_container__content{
  display: flex;
  justify-content: space-between;
  margin-bottom: 9.4vw;
}
.hero_container__news{
  position: relative;
  max-width: 590px;
  margin-top: auto;
  & .news__desc__text-decoration__border:after{
    background-color:#DEDEDE;
  }
  & .hero-article-description .hero-desc__date{
    padding: 25px 0 10px 0;
    text-transform: uppercase;
    color: #DEDEDE;
    font-size: 18px;
    line-height: 24px;
    letter-spacing: -1px;
  }
}
.slide__img{
  width: 100%;
}
.hero__desc__text{
  width: 100%;
  box-sizing: border-box;
  padding-right: 70px;
}


.hero-article-description .news__desc__container {
  font-size: 18px;
  line-height: 24px;
  color: #DEDEDE;
}
.hero-article-description .news__desc__text .news__desc__text-decoration{
  color: #DEDEDE;
}
//slider
.new__slide-container{
  position: relative;
}

.slider-button-container-hero{
  height: 100%;
  width: 33px;
  position: absolute;
  top: 0;
  left: -83px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.swiper-button-prev--news,.swiper-button-next--news, .swiper-button-prev--hero, .swiper-button-next--hero {
  position: initial;
  outline: none;
}
.swiper-button-next--hero{
  width: 150px;
}
.swiper-button-next--hero{
  margin-top:28px;
  outline: none;
}
.hero-desc__date{
  padding: 15px 0 8px 0;
}
.news__desc__container{
  display: flex;
  justify-content: space-between;
  position: relative;
  overflow: hidden;
}
.hero__desc__text:hover .news__desc__text-decoration{
  transform: translate3d(5px, 0px, 0px) scale3d(1, 1, 1) rotateX(0deg) rotateY(0deg) rotateZ(0deg) skew(-8deg, 0deg);
}
.news__desc__text-decoration{
  display: block;
  transition: 0.5s transform linear;
}
@media (max-width: 1680px) {
  .main-title{
    font-size: 110px;
    line-height: 90px;
    font-weight: normal;
  }
  .main-title__sub-title{
    max-width: 630px;
    font-size: 18px;
    line-height: 29px;
  }
  .main-title__sub-title__decoration{
    display: inline-block;
  }
  .hero_container__content{
    margin-bottom: 9.8vw;
  }
  .hero_container__news .hero-article-description .hero-desc__date{
    font-size: 16px;
    line-height: 24px;
  }
  .hero-article-description .news__desc__container{
    font-size: 16px;
    line-height: 24px;
  }
}
@media (max-width: 1439px) and (min-width: 1153px) {
  .main-title{
    font-size: 7.63vw;
    line-height: 82%;
  }
  .main-title__sub-title{
    font-size: 1.25vw;
    line-height: 167%;
  }
  .hero_container__news{
    width: 43%;
  }
  .hero-article-description .news__desc__container{
    font-size: 1.1vw;
    line-height: 150%;
  }
  .hero .hero__desc__text{
    padding-right: 0;
  }
  .hero_container__news .hero-article-description .hero-desc__date{
    font-size: 1.1vw;
    line-height: 150%;
  }
}
@media (max-width: 1153px){
  body{
    height: auto;
  }
  .main{
    height: auto;
    width: 100%;
    overflow: hidden;
  }
  .hero{
    width: 100%;
  }
  .hero_container{
    padding: 30px;
    margin-left: 0;
  }
  .hero_container__news{
    display: none;
  }
  .hero_container__content{
    margin-bottom:0;
    height: calc(100vh - 78px);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .main-title-container{
    width: 100%;
  }
  .main-title{
    font-size: 13vw;
    line-height: 90%;
    max-width: 485px;
  }
  .main-title__sub-title{
    padding-top: 35px;
    max-width: 724px;
    font-size: 24px;
    line-height: 29px;
  }
  .main-title__sub-title__decoration{
    display: inline;
  }
  .navigation{
    display: none;
  }
}