.education-page{
  width: 100vw;
  //padding: 0 50px 0 110px;
}
.education-hero{
  padding: 0 50px 0 110px;
  box-sizing: border-box;
  width: 100vw;
  height: 100vh;
  background-image: url("./img/Education_bg.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.education-logo{
  padding-top: 35px;
  &__svg{
    width: 120px;
  }
}
.educationPage__content{
  max-width: 790px;
  color: #D9D9D9;
  padding-bottom: 9.459vw;
}
.education__content__title{
  font-weight: normal;
  font-size: 110px;
  line-height: 90px;
  text-transform: uppercase;
}
.education__content__sub-title{
  padding-top: 20px;
  max-width: 683px;
  font-family: Open Sans;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 29px;
}
.education__container{
  width: 100vw;
  padding: 90px 50px 0 110px;
  box-sizing: border-box;
}
.title-education__container{
  margin-bottom: 60px;
  border: none;
}
.education__container__description{
  font-family: Open Sans;
  font-weight: normal;
  font-size: 16px;
  line-height: 29px;
  color: #373737;
  padding-bottom: 40px;
}
.education-program{
  width: 100vw;
  padding: 61px 50px 0 110px;
  box-sizing: border-box;
}
.education-program__item{
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.education-program__list{
  border-bottom: 1px solid #dedede;
  &:last-child{
    border: none;
  }
}
.education-program__item__title{
  max-width: 602px;
  font-weight: normal;
  font-size: 24px;
  line-height: 30px;
  letter-spacing: -1px;
  text-transform: uppercase;
  color: #373737;
  padding-top: 28px;
  padding-bottom: 28px;
}

.education-program__item__desc{
  width: 43.7vw;
  margin-left: auto;
  font-family: Open Sans;
  font-weight: normal;
  font-size: 16px;
  line-height: 29px;
  color: #373737;
}
.education-program__list--small{
  padding: 25px 0;
  width: 43.7vw;
  margin-left: auto;
}
.education__list-small{
  position: relative;
  display: flex;
  &:before{
    position: absolute;
    top: 14px;
    content: "";
    width: 7px;
    height: 1px;
    display: inline-block;
    background-color: #373737;
    left: -15px;
  }
}
.education__list-small--no-decoration img{
  display: flex;
  width: 100%;
  height: 100%;
  margin-top: 40px;
}
.education__list-small--no-decoration:before{
  display: none;
}
.education_list-small__text{
  font-family: Open Sans;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 29px;
  color: #373737;
}
.other-ways-education{
  padding-top: 256px;
}
.title--other-ways-education{
  display: block;
  margin-left: 100px;
  margin-right: 50px;
  width: calc(100% - 110px - 50px);
  padding-bottom: 90px;
  border-bottom: 1px solid #dedede;
}
.other-ways-education--container{
  margin-top: 90px;
  padding: 0 50px 0 110px;
  box-sizing: border-box;
  background: url("./img/Direction.jpg") no-repeat center;
  background-size: cover;
}
.other-ways-education--contact{
  padding: 260px 50px 103px 110px;
}
.title-education__container span{
  margin-top: 2px;
  padding-bottom: 2px;
  display: block;
  border-bottom: 2px solid #373737;
}
.title-education__container span:last-child{
  display: inline-block;
}

