.services-mobile-content{
  background-image: url("./img/services.jpg");
  min-height: 1144px;
  &:after{
    background-color: rgba(0,0,0,.5);
  }
  .mobile-content__description{
    padding-top: 40px;
  }
}
.show {
  & .brand-mobile-list__content{
    display: block;
    height: auto;
    max-height: 800px;
    min-height: 100%;
    transition: 0.5s all linear ;
    & .mobile-content__description--services{
      padding-top: 40px;
      transition: 0.8s padding linear 0.3s;
    }
    & .mobile-content__description--services:last-child{
      padding-top: 20px;
    }
  }
}
.services-bg-item__img{
  padding: 30px 35px 0 30px;
}

